:root {
  --color-header: #311253;
  --color-footer: #470202;
  --color-section-1: #d3e0ff;
  --color-section-2: #aff8af;
  --color-section-2a: #fac4ff;
  --color-section-2b: #ffacac;
  --color-section-2b1: #cdb3ff;
  --color-section-2b2: #fffcce;
  --color-section-3: #ffd1bb;
  --font-main: system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
  --radius: 12px;
  --shadow: 0 2px 6px #0000000f;
  --space-1: clamp(.5rem, 1.5vw, .75rem);
  --space-2: clamp(.75rem, 2vw, 1rem);
  --space-3: clamp(1rem, 2.5vw, 1.25rem);
  --space-4: clamp(1.25rem, 3vw, 1.5rem);
}

html, body {
  height: 100%;
}

body {
  font-family: var(--font-main);
  background: #fff;
  grid-template-rows: auto 1fr auto;
  min-block-size: 100%;
  margin: 0;
  display: grid;
}

h1 {
  color: #fff;
}

h2 {
  color: #000;
}

.msg--error, .msg--success, .msg--info {
  border: 1px solid #00000014;
  border-radius: 6px;
  margin: 12px 0;
  padding: 12px 16px;
  line-height: 1.4;
}

.msg--info {
  color: #0d47a1;
  background: #e3f2fd;
}

.msg--success {
  color: #1b5e20;
  background: #e8f5e9;
}

.msg--error {
  color: #b71c1c;
  background: #ffebee;
}

.msg--error a {
  font-weight: 700;
}

.site-header {
  background: var(--color-header);
  color: #fff;
  padding-block: var(--space-2);
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.site-header .logo {
  left: var(--space-2);
  position: absolute;
}

.site-header .title {
  margin: 0;
}

.site-footer {
  background: var(--color-footer);
  color: #fff;
  padding-block: var(--space-2);
  text-align: center;
}

.layout {
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "nav"
                       "content"
                       "testimonials";
  display: grid;
}

.nav {
  background: var(--color-section-1);
  padding: var(--space-3);
  grid-area: nav;
}

.nav__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  outline-offset: 2px;
  border-radius: 999px;
  padding: .4rem .75rem;
  text-decoration: underline;
}

.content {
  padding: var(--space-3);
  gap: var(--space-3);
  background: #fff;
  grid-area: content;
  grid-template-columns: 1fr 3fr;
  display: grid;
}

.content__menu {
  background: var(--color-section-2a);
  padding: var(--space-3);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  gap: var(--space-2);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 540px;
  display: flex;
}

.content__main {
  background: var(--color-section-2b);
  padding: var(--space-3);
  border-radius: var(--radius);
  box-shadow: var(--shadow);
  gap: var(--space-3);
  display: grid;
}

.description {
  background: var(--color-section-2b1);
  padding: var(--space-3);
  border-radius: var(--radius);
  min-height: 290px;
  margin: 0;
}

.testimonial, .products__item {
  border-radius: var(--radius);
  padding: var(--space-3);
  text-align: center;
  box-shadow: var(--shadow);
  background: #fff;
  border: 1px solid #00000014;
}

.products {
  background: var(--color-section-2b2);
  padding: var(--space-3);
  border-radius: var(--radius);
}

.products__list {
  gap: var(--space-4);
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.products__item {
  background: var(--color-section-1);
}

.testimonials {
  background: var(--color-section-3);
  padding: var(--space-3);
  gap: var(--space-2);
  flex-wrap: wrap;
  grid-area: testimonials;
  justify-content: center;
  align-items: center;
  display: flex;
}

.testimonial {
  background: var(--color-section-2);
  width: 200px;
  height: 30px;
}
